main {
  padding: 12vw;
  position: relative;
}

.main-image {
  width: 45vw;
}

.main-description-container {
  position: absolute;
  left: 48%;
  bottom: 7vw;
  width: 45vw;
  background-color: white;
  padding: 5vw;
}

.main-description-container > h2 {
  font-size: 2vw;
  font-weight: 200;
  margin-bottom: 1rem;
}

.main-description-container > p {
  font-size: 1vw;
  color: gray;
}

/********* media query *********/

@media (max-width: 1023px) {
  main {
    padding: 18vw 10vw 16vw;
    margin: auto;
  }

  .main-description-container > h2 {
    font-size: 3.3vw;
  }

  .main-description-container > p {
    font-size: 1.5vw;
  }
}

@media (max-width: 769px) {
  .main-image {
    width: 50vw;
  }

  .main-description-container {
    left: 43%;
    width: 50vw;
  }

  .main-description-container > h2 {
    margin-bottom: 1rem;
  }
}

@media (max-width: 602px) {
  main {
    padding: 18vw 5vw 16vw;
  }

  .main-image {
    width: 100%;
  }

  .main-description-container {
    position: unset;
    width: 100%;
    padding: 5vw 0;
  }

  .main-description-container > h2 {
    font-size: 5vw;
    font-weight: 200;
    margin-bottom: 1rem;
  }

  .main-description-container > p {
    font-size: 2.5vw;
  }
}

@media (max-width: 466px) {
  .main-description-container {
    text-align: center;
  }

  .main-description-container > h2 {
    font-size: 5vw;
    margin-bottom: 9px;
  }

  .main-description-container > p {
    font-size: 3.2vw;
    line-height: 1.5;
  }
}
