.projects-wrapper {
  padding: 0 10rem 10rem;
}

.projects-heading {
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
  font-size: 2vw;
}

.projects-content {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 1.3rem;
  margin-top: 1.5rem;
}

.deep-earth {
  background-image: url("../../images/desktop/image-deep-earth.jpg");
}

.night-arcade {
  background-image: url("../../images/desktop/image-night-arcade.jpg");
}

.soccer-team {
  background-image: url("../../images/desktop/image-soccer-team.jpg");
}

.the-grid {
  background-image: url("../../images/desktop/image-grid.jpg");
}

.from-up-above {
  background-image: url("../../images/desktop/image-from-above.jpg");
}

.pocket-borealis {
  background-image: url("../../images/desktop/image-pocket-borealis.jpg");
}

.curiosity {
  background-image: url("../../images/desktop/image-curiosity.jpg");
}

.fisheye {
  background-image: url("../../images/desktop/image-fisheye.jpg");
}

.project-wrapper {
  position: relative;
}

.project {
  background-repeat: no-repeat;
  background-size: cover;
  height: 60vh;
  background-position: center;
  cursor: pointer;
  transition: opacity 0.5s;
}

.project:hover {
  opacity: 0.5;
}

.project-wrapper:hover .proj-heading {
  color: black;
}

.proj-heading {
  position: absolute;
  bottom: 0;
  color: white;
  font-size: 2rem;
  padding: 2.5rem;
  font-weight: 200;
  transition: color 0.5s;
}

.project-btn {
  border: 1px black solid;
  font-size: 1.3vw;
  letter-spacing: 4px;
  font-weight: bolder;
  padding: 0.6rem;
  width: 15vw;
  cursor: pointer;
  transition: background-color 0.5s, color 0.5s;
}

.project-btn:hover {
  background-color: black;
  color: white;
}

/********* media query *********/

@media (max-width: 1326px) {
  .project {
    height: 50vh;
  }

  .proj-heading {
    font-size: 2.1vw;
    padding: 2vw;
  }
}

@media (max-width: 1023px) {
  .project {
    height: 23vw;
    background-position: center;
  }

  .proj-heading {
    font-size: 2.5vw;
  }

  .projects-wrapper {
    padding: 0 10vw 13vw;
  }
}

@media (max-width: 602px) {
  .projects-content {
    grid-template-columns: 1fr;
    margin-top: 0.7rem;
    grid-gap: 1.1rem;
    width: 100%;
  }

  .projects-wrapper {
    padding: 0 5vw 40vw;
    position: relative;
  }

  .project-btn {
    font-size: 3.7vw;
    position: absolute;
    right: 50vw;
    bottom: 23vw;
    transform: translateX(50%);
    width: 37vw;
  }

  .project {
    background-position: top;
  }

  .projects-heading {
    font-size: 3vw;
  }

  .proj-heading {
    font-size: 3.8vw;
  }
}
