#heading {
  background-image: url("../../images/desktop/image-hero.jpg");
  width: 100%;
  height: 75vh;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  padding: 0 8rem;
}

.navbar-wrapper {
  display: flex;
  justify-content: space-between;
  padding-top: 3rem;
  height: 6rem;
  position: relative;
}

.navbar-container {
  display: flex;
  align-items: center;
  gap: 4vw;
  position: relative;
}

.navbar-link {
  font-size: 1.1vw;
  color: white;
}

.header-wrapper {
  height: 80%;
  display: flex;
  align-items: center;
}

.header-container {
  width: 40vw;
  border: 3px white solid;
  padding: 3rem;
}

.header-title {
  color: white;
  letter-spacing: 2px;
  font-size: 3.5vw;
  font-weight: 200;
  line-height: 1.2;
}

/**************** mobile menu btn ****************/

.mobile-menu-btn {
  width: 100%;
  position: relative;
  display: flex;
  align-items: flex-end;
  flex-direction: column;
}

.mobile-menu-svg {
  display: none;
  width: 2rem;
  height: 2rem;
  color: white;
}

/**************** mobile menu ****************/

.mobile-menu {
  z-index: 10;
  position: absolute;
  top: 0rem;
  left: 0;
  width: 100vw;
  height: 100%;
  background-color: black;
  padding: 2.75rem 3rem;
}

.mobile-menu-top {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.close-btn {
  width: 7vw;
}

.mobile-item a {
  color: white;
  font-family: "Josefin Sans", sans-serif;
  font-size: 5.5vw;
}

.mobile-item a:hover::after {
  width: 0;
}

.mobile-menu-container {
  height: 100%;
  display: flex;
  align-items: center;
}

.mobile-menu-ul {
  display: flex;
  gap: 2rem;
  flex-direction: column;
}

/**************** media query ****************/

@media (max-width: 1023px) {
  .navbar-container {
    gap: 2.8vw;
  }

  #heading {
    padding: 0 5rem;
  }

  .navbar-item > a {
    font-size: 2vw;
  }
}

@media (max-width: 769px) {
  #heading {
    padding: 0 3rem;
  }

  .navbar-item > a {
    font-size: 2vw;
  }

  .header-container {
    width: 55vw;
  }

  .navbar-container {
    gap: 2.5vw;
  }

  .header-title {
    font-size: 5vw;
  }
}

@media (max-width: 603px) {
  .header-container {
    width: 65vw;
  }

  .logo {
    width: 30vw;
  }

  .navbar-wrapper {
    align-items: center;
    padding-top: 1.5rem;
  }

  .header-title {
    font-size: 6vw;
  }

  .header-wrapper {
    height: 70%;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .navbar-item {
    display: none;
  }

  .mobile-menu-svg {
    display: block;
  }
}

@media (max-width: 466px) {
  #heading {
    padding: 0 2rem;
  }

  .header-container {
    width: 100%;
    padding: 1.5rem;
  }

  .header-title {
    font-size: 10vw;
    line-height: 1;
  }

  .logo {
    width: 40vw;
  }
}

@media (max-width: 425px) {
  .header-title {
    font-size: 8vw;
  }
}
