footer {
  background-color: black;
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2rem 10vw;
}

.footer-about-section,
.copyright {
  margin-top: 1rem;
}

.footer-socials {
  display: flex;
  flex-direction: column;
}

.socials-container {
  display: flex;
  gap: 1rem;
  justify-content: flex-end;
}

.socials {
  max-width: 2vw;
}

.copyright {
  font-size: 0.8vw;
  color: gray;
}

.footer-item a {
  color: white;
}

/********* media query *********/

@media (max-width: 1023px) {
  .copyright {
    font-size: 1.1vw;
  }
}

@media (max-width: 602px) {
  footer {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    gap: 2rem;
  }

  .socials-container {
    justify-content: center;
  }

  .navbar-container {
    display: flex;
    flex-direction: column;
    gap: 1.2rem;
    margin: 2rem 0;
  }

  .navbar-item a {
    font-size: 1.2rem;
  }

  .copyright {
    font-size: 0.6rem;
  }

  .socials {
    max-width: 7vw;
  }
}
